<template>
  <v-main class="black">
    <div class="pa-8"></div>
    <v-card height="900" width="600" class="mx-auto card">
      <div class="pa-4"></div>
      <v-card class="pa-7" flat color="transparent">
        <v-container>
          <v-row justify="center" align="start" class="text-center">
            <v-img
              :src="require('@/assets/images/cmet_logo.webp')"
              max-width="60%"
              :min-width="'calc(70% - ' + $vuetify.application.top + 'px)'"
            />
          </v-row>
        </v-container>
      </v-card>
      <div class="pa-6"></div>

      <v-container class="mb-6">
        <v-row justify="center">
          <v-form ref="form" v-model="valid" lazy-validation>
            <v-container>
              <v-row justify="start">
                <v-col cols="12">
                  <v-text-field
                    dark
                    outlined
                    v-model="user.email"
                    :rules="rules.email"
                    required
                    clearable
                  >
                    <template v-slot:label>
                      <p class="mx-4">E-mail</p>
                    </template>
                  </v-text-field>
                </v-col>
                <v-col cols="12">
                  <v-text-field
                    type="password"
                    dark
                    outlined
                    v-model="user.pwd"
                    :rules="rules.pwd"
                    required
                    clearable
                  >
                    <template v-slot:label>
                      <p class="mx-4">Choose Password</p>
                    </template>
                  </v-text-field>
                </v-col>
                <v-col cols="12">
                  <v-text-field
                    type="password"
                    dark
                    outlined
                    v-model="user.repwd"
                    :rules="[repwd]"
                    required
                    clearable
                  >
                    <template v-slot:label>
                      <p class="mx-4">Retype password</p>
                    </template>
                  </v-text-field>
                </v-col>
                <v-col cols="12">
                  <VuePhoneNumberInput
                    class="phoneInput mb-2"
                    v-model="phoneNumber"
                    dark
                    dark-color="transparent"
                    error-color="red"
                    valid-color="green"
                    size="xl"
                    :disabled="disabled"
                    :default-country-code="defaultCountry"
                    :loader="hasLoaderActive"
                    clearable
                    :error="hasErrorActive"
                    @update="onUpdate"
                  />
                </v-col>
              </v-row>
              <div class="pa-6"></div>
              <v-row justify="center">
                <v-col cols="12" class="text-center">
                  <v-btn
                    type="submit"
                    class="login-btn input"
                    depressed
                    rounded
                    elevation="0"
                    color="#e2543a"
                    dark
                    large
                    :disabled="!valid"
                    @click="submit"
                  >
                    Create account
                  </v-btn>
                </v-col>
                <v-col cols="12" class="text-center">
                  <span class="white--text pt-6">Already have an account?</span>
                  <v-btn
                    class="login-btn mb-1"
                    text
                    depressed
                    dark
                    :to="{ name: 'Login' }"
                  >
                    <span style="font-size: 14px">Sign in</span>
                  </v-btn>
                </v-col>
              </v-row>
            </v-container>
          </v-form>
        </v-row>
      </v-container>
    </v-card>
    <div class="pa-12"></div>
  </v-main>
</template>

<script>
import { register } from "../api";
import VuePhoneNumberInput from "vue-phone-number-input";
import "vue-phone-number-input/dist/vue-phone-number-input.css";

export default {
  components: {
    VuePhoneNumberInput,
  },
  data: () => ({
    valid: true,
    date: null,
    phoneNumber: null,
    defaultCountry: "TW",
    results: {},
    disabled: false,
    hasLoaderActive: false,
    hasErrorActive: false,
    user: {
      email: "",
      pwd: "",
      repwd: "",
      name: "",
      phone: "",
    },
    rules: {
      email: [
        (v) => !!v || "Please enter E-mail",
        (v) => /.+@.+\..+/.test(v) || "Please enter the correct E-mail",
      ],
      pwd: [
        (v) => !!v || "Please enter the password",
        (v) =>
          /^[a-zA-Z0-9]+$/.test(v) ||
          "Please enter uppercase and lowercase letters or numbers",
        (v) =>
          (v && v.length <= 12) ||
          "Password length must be less than 12 characters",
        (v) =>
          (v && v.length >= 8) ||
          "Password length must be more than 8 characters",
      ],
      phone: [
        (v) => !!v || "Please enter your phone",
        (v) => /^[09]{2}[0-9]{8}$/.test(v) || "",
      ],
    },
  }),
  methods: {
    repwd(val) {
      if (val !== this.user.pwd) {
        return "Inconsistent passwords";
      }
      return true;
    },

    onUpdate(payload) {
      this.results = payload;
      console.log(this.results);
    },

    submit: function () {
      this.$refs.form.validate();

      if (this.$refs.form.validate() == true) {
        const params = new URLSearchParams();
        params.append("Email", this.user.email);
        params.append("PassWod", this.user.pwd);
        params.append("Mobile_Num", this.phoneNumber.replace(/\s*/g, ""));

        register(params)
          .then(() => {
            alert("Register Success");
            this.$router.push({ name: "Login" });
          })
          .catch((err) => {
            console.error(err);
          });
      }
    },
  },
};
</script>

<style lang="css" scoped>
.input {
  background: linear-gradient(to right, #00a199 0%, #00e6b4 100%);
}

.login-btn span {
  text-transform: capitalize;
  font-weight: normal;
}

.card {
  background: linear-gradient(to bottom, #002832 0%, #000000 100%);
}

.phoneInput input {
  height: 56px;
}
</style>